@import url(https://fonts.googleapis.com/css2?family=Bitter:wght@500&family=Noto+Serif:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@700&display=swap);
* {
  scroll-behavior: smooth;
}

body {
  margin: 0; padding: .6rem .2rem 4rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-break-container {
    height: 10vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    overflow: hidden;
}
.app-break {
    border-bottom: solid .1rem var(--ul-background);
    border-right: solid .1rem var(--ul-background);
    box-shadow: -.1rem 0 1rem var(--ul-background);
    background: var(--ul-background-semi-transparent);
    margin: .2rem;
    width: 10px; height: 10px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

:root {
    --li-background: #7E8B92;
    --ul-background: #5F9EA0;
    --ul-background-semi-transparent: #5F9EA022;
}

.dl_app {
    margin: 2rem auto; padding: auto;
    width: 100%; height: -webkit-fit-content; height: -moz-fit-content; height: fit-content;
}
.dl_app h1 {
    margin: auto;
    font-family: 'Noto Serif', serif;
    text-align: center;
}
.dl_app .dl-container {
    border: solid .2rem #5F9EA0;
    border: solid .2rem var(--ul-background);
    box-shadow: 0 0 .8rem #5F9EA0;
    box-shadow: 0 0 .8rem var(--ul-background);
    border-radius: .8rem;
    margin: 2rem auto; padding: 0.8rem 1.2rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content; height: -webkit-fit-content; height: -moz-fit-content; height: fit-content;
    background: #5F9EA022;
    background: var(--ul-background-semi-transparent);
}
.dl_app .dl-container ul {
    margin: 0; padding: 0;
    list-style: none;
}
.dl_app .dl-container ul li {
    border-radius: .2rem;
    margin: .8rem auto; padding: .8rem 2rem;
    box-shadow: 0 0 1rem #7E8B92;
    box-shadow: 0 0 1rem var(--li-background);
    background: #7E8B92;
    background: var(--li-background);
    max-width: 74vw;
    overflow-wrap: normal;
    color: #fff;
    font-family: 'Bitter', serif;
    font-size: 1.6rem;
    font-weight: 500;
    opacity: 1;
    cursor: grab;
    cursor: -webkit-grab;
}
.dl_app .dl-container ul li.dragging {
    opacity: .6;
    cursor: grabbing;
    cursor: -webkit-grabbing;
}

:root {
    --text: #102030;
}

.tw_app {
    margin: 2rem auto; padding: auto;
    width: 100%; height: -webkit-fit-content; height: -moz-fit-content; height: fit-content;
}
.tw_app h1 {
    margin: auto; padding: 0;
    text-align: center;
    font-family: 'Noto Serif', serif;
}
.tw_app .text-container {
    margin: 2rem; padding: 1rem 2rem;
}
.tw_app .text-container h3 {
    border-radius: .2rem;
    margin: .8rem 0; padding: .8rem 2rem;
    box-shadow: 0 0 1rem var(--li-background);
    background: var(--li-background);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: #E6E6FA;
    /* margin: 0; padding: .8rem; */
    text-align: left;
}
.tw_app .text-container p {
    border: solid .2rem var(--ul-background);
    box-shadow: 0 0 .8rem var(--ul-background);
    border-radius: .8rem;
    margin: 0; padding: .8rem 2rem;
    max-height: 50vh;
    background: var(--ul-background-semi-transparent);
    color: #102030;
    color: var(--text);
    overflow-y: scroll;
    text-align: left;
    font-size: 18px;
    line-height: 1.6rem;
}
.tw_app .text-container p::-webkit-scrollbar {
    display: none;
}
.tw_app .feedback-container {
    margin: 2rem 4rem; padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}
.tw_app .feedback-container .score-container,
.tw_app .feedback-container .error-count-container,
.tw_app .feedback-container .timer-container {
    border: solid .2rem var(--ul-background);
    box-shadow: 0 0 .8rem var(--ul-background);
    margin: auto; padding: 1rem;
    border-radius: .4rem;
    background: var(--ul-background-semi-transparent);
    min-height: 1rem;
    -webkit-flex: 1 1;
            flex: 1 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-transform: skew(30deg);
            transform: skew(30deg);
}
.tw_app .feedback-container .score-container p,
.tw_app .feedback-container .error-count-container p,
.tw_app .feedback-container .timer-container p {
    margin: .4rem .6rem;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-transform: skew(-30deg);
            transform: skew(-30deg);
}
.tw_app .input-text-container {
    margin: 2rem; padding: 1rem 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.tw_app .input-text-container textarea {
    border: solid .2rem var(--ul-background);
    box-shadow: 0 0 .8rem var(--ul-background);
    border-radius: .8rem;
    margin: auto; padding: .8rem 2rem;
    width: 100%; min-height: 10vh; max-height: 50vh;
    background: var(--ul-background-semi-transparent);
    color: #102030;
    color: var(--text);
    overflow-y: scroll;
    text-align: left;
    font-size: 18px;
    line-height: 1.6rem;
    resize: vertical;
}
.tw_app .input-text-container textarea::-webkit-scrollbar {
    display: none;
}
.tw_app .input-text-container textarea:focus {
    outline: none;
}


